@tailwind base;
@tailwind components;
@tailwind utilities;

.CardList {
    @apply text-center border border-black overflow-y-scroll;
    @apply w-2/5 h-full max-lg:w-4/5;
}

.Card {
    @apply inline-flex flex-col items-center justify-center rounded overflow-hidden shadow-lg shadow-slate-400 my-5 border-4;
    @apply hover:bg-slate-100 border-[color:var(--lightgreen)] hover:border-[color:var(--green)];
    @apply w-1/3 h-48;
}

.Card-Title {
    @apply font-bold text-3xl mb-2 px-6 py-4 max-sm:text-xl max-lg:text-2xl;
}

.Card-Detail {
    @apply flex flex-row items-center justify-center max-md:flex-col;
}

.Card-Detail-Stroke {
    @apply bg-gray-200 rounded-full px-3 py-1 text-sm font-semibold text-gray-700 max-md:mb-2;
}
